// 
// 
const pathPre = '/data'
//  
export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/data/Main.vue'))
        })
    },
    children: [
        {
            path: 'category',
            name: 'data-category',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/data/category.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        //
        {
            path: 'testing',
            name: 'data-testing',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/data/Testing.vue'))
                })
            },
            meta: {
                title: ' -  ',
                noback: true
            }
        },
        {
            path: 'add',
            name: 'data-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/data/Form.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'edit/:sys_id',
            name: 'data-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/data/Form.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        // //gov
        //  {
        //     path: 'user-loginlog',
        //     name: 'data-user-loginlog',
        //     component: resolve => {
        //         require.ensure([], () => {
        //             resolve(require('../views/data/UserLoginlog.vue'))
        //         })
        //     },
        //     meta: {
        //         title: ' -  ',
        //         noback: true
        //     }
        // },
        //
        {
            path: 'course-history',
            name: 'data-course-history',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/data/companyStat/Index.vue'))
                })
            },
            meta: {
                title: ' - ',
                noback: true
            }
        },
        {
            path: 'company/stat/view',
            name: 'data-company-stat-view',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/data/companyStat/View.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        //
        {
            path: 'course-log',
            name: 'data-course-log',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/data/CourseLog.vue'))
                })
            },
            meta: {
                title: ' - ',
                noback: true
            }
        },
        //
         {
            path: 'report-userlogin',
            name: 'data-report-userlogin',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/data/ReportUserlogin.vue'))
                })
            },
            meta: {
                title: ' -  ',
                noback: true
            }
        },
        //
        {
            path: 'report-userlogin/:gov_id',
            name: 'data-report-user-user',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/Admin.vue'))
                })
            },
            meta: {
                title: '',
            }
        },
        
        {
            path: 'report-userlogin/:gov_id',
            name: 'data-report-userlogin-user',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/Admin.vue'))
                })
            },
            meta: {
                title: '',
            }
        },
        {
            path: 'report-nouserlogin/:gov_id',
            name: 'data-report-nouserlogin-user',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/gov/Admin.vue'))
                })
            },
            meta: {
                title: '',
            }
        },
    ]
}



