// 
const pathPre = '/exam'
export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/exam/Main.vue'))
        })
    },
    children: [
 
        //
        {
            path: 'course/category', 
            name: 'exam-course-category',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/exam/course/category.vue'))
                })
            },
            meta: {
                title: '-',
                noback: true
            }
        },
        {
            path: 'course/manage',
            name: 'exam-course-manage',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/exam/course/CourseManage.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        {//
            path: 'course/add',
            name: 'exam-course-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/exam/course/ExamCourseForm.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'course/edit/:id',
            name: 'exam-course-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/exam/course/ExamCourseForm.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        //
        {
            path: 'chapter/category',
            name: 'exam-chapter-category',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/exam/chapter/category.vue'))
                })
            },
            meta: {
                title: '-',
                noback: true
            }
        },
        {
            path: 'chapter/manage',
            name: 'exam-chapter-manage',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/exam/chapter/TestPaperManage.vue'))
                })
            },
            meta: {
                title: ' ',
                noback: true
            }
        },
        {//
            path: 'subject/add',
            name: 'exam-subject-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/exam/chapter/ExamSubjectForm.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'subject/edit/:id',
            name: 'exam-subject-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/exam/chapter/ExamSubjectForm.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        //
        {
            path: 'category',
            name: 'exam-category',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/exam/course/ExamCategory.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
    ]
}



