// 
// 
const pathPre = '/section'
//  
export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/section/Main.vue'))
        })
    },
    children: [
        {
            path: 'category',
            name: 'section-category',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/section/category.vue'))
                })
            },
            meta: {
                title: ' - ',
                noback: true
            }
        },
        //
        {
            path: 'data',
            name: 'section-data',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/section/Index.vue'))
                })
            },
            meta: {
                title: '-',
                noback: true
            }
        },
        //
        // {
        //     path: 'data',
        //     name: 'section-data',
        //     component: resolve => {
        //         require.ensure([], () => {
        //             resolve(require('../views/section/SecData.vue'))
        //         })
        //     },
        //     meta: {
        //         title: '-',
        //         noback: true
        //     }
        // },
        {
            path: 'add', 
            name: 'section-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/section/Form.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'edit/:sys_id',
            name: 'section-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/section/Form.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
         {
            path: 'app/menu',
            name: 'section-app-menu',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/section/appmenu/Index.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
    ]
}



