// 
// 
const pathPre = '/setting'
//  
export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/setting/Main.vue'))
        })
    },
    children: [
        //
        {
            path: 'lists',
            name: 'setting-list',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/setting/Index.vue'))
                })
            },
            meta: {
                title: '',
                noback: true
            }
        },
        {
            path: 'add',
            name: 'setting-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/setting/Form.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
        {
            path: 'edit/:sys_id',
            name: 'setting-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/setting/Form.vue'))
                })
            },
            meta: {
                title: '',
                noback: false
            }
        },
    ]
}



